@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://cdn.jsdelivr.net/npm/@xz/fonts@1/serve/cascadia-code.min.css);
*{margin:0;padding:0;text-decoration:none;box-sizing:border-box;font-family:"Roboto", sans-serif;color:white}p,h4{max-width:60ch}a,img{-webkit-user-select:none;-ms-user-select:none;user-select:none}#root,html,body{width:100%}body{background-color:#007cc7}.notBlueSelect::selection,.notBlueSelect *::selection{background:#12232e}

.codeblock{background-color:#4da8da;width:35%;margin:0 auto;padding:5px;border-radius:5px;box-shadow:0px 0px 5px #203647}.codeblock>div{padding:3px;border-radius:5px;background-color:#12232e;color:#eefbfb;box-shadow:0px 0px 5px #203647}.codeblock>div .codeline{margin:5px 1.3ch;line-height:1.6}.codeblock>div .codeline p{font-family:"Cascadia Code", sans-serif;display:inline;-webkit-user-select:none;-ms-user-select:none;user-select:none}.cursor{-webkit-animation:blink 3s infinite;animation:blink 3s infinite;color:transparent}.Typist{display:inline}@-webkit-keyframes blink{0%{background-color:transparent}25%{background-color:white}50%{background-color:transparent}75%{background-color:white}100%{background-color:transparent}}@keyframes blink{0%{background-color:transparent}25%{background-color:white}50%{background-color:transparent}75%{background-color:white}100%{background-color:transparent}}

#content{max-width:1100px;margin:0 calc((100% - 1100px) / 2)}.intro{text-align:justify;margin:0 auto;line-height:1.6;border:1px dashed #000000;padding:16px;border-radius:10px}

.project{width:60%;background-color:#4da8da;height:150px;border-radius:15px;box-shadow:0px 0px 7px #203647;position:relative}.project .imageDiv{width:150px;height:100%}.project .imageDiv img{margin:15px;height:120px;border-radius:25%}.project .info{position:absolute;top:15px;left:150px;max-width:50ch}.project .info h3{margin-bottom:5px}.project .status{position:absolute;bottom:15px;left:150px;background-color:#12232e;box-shadow:0px 0px 5px #203647;padding:7px;border-radius:10px}.project .madeWith{background-color:#12232e;padding:5px;position:absolute;top:12.5px;right:12.5px;border-radius:7px}.project .madeWith p{height:1em}.project .madeWith p svg{margin-right:7px}.project .madeWith p svg:last-of-type{margin-right:0}.project .icons{position:absolute;right:15px;bottom:11px}.project .icons a{font-size:1.15em;margin-left:7px}


#header{background-color:#12232e;padding:0.05em 0;box-shadow:0 0 15px 0 #203647}#header>div{margin:1.25em 2.5%}#header>div h1{display:inline;color:#eefbfb}#header>div span{float:right;margin:0.345em 0}#header>div span a{margin-left:1.25em;display:inline;font-size:2em;color:#eefbfb}

